const axios = require('axios');
// 
export async function _order(data){

    const url_order='http://localhost/cofanew/wp-content/themes/danhdev_1/templates/ajax/admin-components/checkout/add_checkout.php';
    let data_send=new FormData();
    data_send.append('data',JSON.stringify(data));
    let response= await axios.post(url_order, 
        data_send
    )
    .then(function (response) {
        return response.data.status         
    })
    .catch(function (error) {
        console.log(" ~ file: axios.js ~ line 97 ~ action_create_or_edit_post ~ error", error)
        return false
    })
    return response;
}
export  function _telegram(data){
    const url_order='http://localhost/cofanew/wp-content/themes/danhdev_1/templates/ajax/admin-components/checkout/telegram.php';
    let data_send=new FormData();
    data_send.append('data',JSON.stringify(data));
    axios.post(url_order, data_send)
    .then(function (response) {
        return response.data.status         
    })
}

 