import React, { Component } from 'react';
import './header.css'
import {get_icon,phone_zalo,icon_animation} from '../../lib/fs';
export default class Header extends Component {
  constructor (props) {
    super(props)
    this.state = {
      menu_mb:false
    }
  }
  render() {
    let {menu_mb}=this.state;
    let {common}=this.props;
    return (
      <React.Fragment>
          <header id="header" className="header header-wrapper" >
              <div className='colorz header-top'>
                <div className='container'>
                  <div className='row'>
                    <div className='col-12 col-lg-6 pd8'>

                      <p className="zxs">
                         {common.header.chao_mung}
                        {common.header.chao_mung_url!=''&&<a href={common.header.chao_mung_url} className="text-bold">Xem ngay</a> }
                      </p>

                    </div>
                    <div className='col-lg-3 hd992 pd8'>
                      <p className="zxs tal-r">
                        {get_icon("phone","white","16px",{marginRight:"5px",transform:' rotate(-45deg)',marginBottom:"5px"})}
                        Hotline: {phone_zalo(common.lien_he.sdt_hotline)}
                      </p>
                    </div>
                    <div className='col-lg-3 hd992 pd8'>
                       <p className="zxs cshv tal-r">
                       {get_icon("user","white","16px",{marginRight:"5px",marginBottom:"5px"})}
                        ng nhp/ng k
                       </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className='header-main' style={{backgroundImage:`url(${common.header.background})`}}>
                <div className='icon-menu' onClick={()=>this.setState({menu_mb:!menu_mb})}>
                  {get_icon("menu","rgb(249 211 211)","32px",{marginRight:"5px",marginBottom:"5px"})}
                </div>

                <div className='icon-cart'>
                  <a href='#' style={{textDecoration:"none"}}>
                    <div className='wrap-icon-cart'>
                      {get_icon("cart","#ffd41b","32px",{marginRight:"5px",marginBottom:"5px"})}<span className='gh'>Gi hng</span><span className='nuvbe'>(0)</span>
                    </div>
                  </a>
                </div>
                <div className='container setz'>
                  <div className='row'>
                    <div className='col-12 col-lg-3 brz'>
                      <div className='logo'>
                        <a href={window.location.origin}>
                          {common.header.logo!=''&&<img src={common.header.logo} className="header-logo-dark" alt="alt logo here"/>}
                        </a>
                      </div>
                    </div>
                    <div className='col-12 col-lg-6 seah'>
                      <form>
                        <div className="input-group">
                          <input type="search" name="s" placeholder="Tm kim..." aria-describedby="button-addon5" className="form-control" autoComplete="off"/>
                          <div className="input-group-append">
                            <button id="button-addon5" type="submit" className="btn brs" style={{background:"#ff9800"}}>
                            {get_icon("search","white","16px")}
                            </button>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
              <div className='colorz header-bottom colorz'>
                <div className='container wr-menu '>
                  <div id="set-menu" className={menu_mb?'set-menu':'set-menu menu-mb'}>
                      <div className='menu'>
                        <ul>
                            {
                              common.header.menu.map((item,i)=>{
                                return(
                                  <li key={item.id}>
                                  <a href={item.url} title={item.name} >{item.name}
                                  {icon_animation(item.icon)}
                                  </a>
                                </li>
                                )
                              })
                            }
                        </ul>
                        {/* <ul>
                          <li>
                            <a onClick={()=>console.log("a")}>Trang ch</a>
                          </li>
                          <li>
                            <a>Ti khon
                              <span className='newz'>
                                {get_icon("new","white","39px",{marginLeft:"12px",marginBottom:"4px"})}
                              </span>
                            </a>
                          </li>
                          <li>
                              <a>V Nest ART 
                              </a>
                              <ul>
                                <li><a>Web cui</a></li>
                              </ul>
                          </li>
                          <li>
                            <a>B qu tng</a>
                          </li>
                          <li>
                            <a>Danh mc sn phm</a>
                            <ul>
                              <li><a>Danh mc 1 toi mun n di thm 
                              <span className='newz'>
                                {get_icon("gift","white","20px",{marginLeft:"12px",marginBottom:"4px"})}
                              </span>
                              </a></li>
                              <li><a>Danh mc 2</a></li>
                              <li><a>Danh mc 3</a></li>
                              <li><a>Danh mc 4 ngn hn</a></li>
                            </ul>
                          </li>
                         
                        </ul> */}
                      </div>
                      <div className='dimmer' id='dimmer' onClick={()=>this.setState({menu_mb:!menu_mb})}><span>x</span></div>
                  </div>
                </div>
              </div>
          </header>
      </React.Fragment>
    );
  }
}

