import React, { Component } from 'react';
import './footer.css'
import {get_icon,phone_zalo} from '../../lib/fs';
import bct from '../../lib/media/bct.png';
import call from './call.png';
import phoneIcon from './call.svg';
import ZaloIcon from './zalo.svg';
import fbIcon from './fb.svg';

export default class Footer extends Component {
  constructor (props) {
    super(props)
    this.state = {}
  }
  render() {
    let {common}=this.props
    let zalo_number_text=phone_zalo(common.lien_he.sdt_hotline,'.')
    return (
        <React.Fragment>
            <footer id="footer">
                <div className='bg1'>{get_icon("bg1","white","100%",{transform: 'rotate(180deg)'})}</div>
                
                <div className='content-footer'>
                    <div className='container'>
                        <div className='row'>
                            <div className='col-12 col-sm-6 col-lg-3'>
                                <div className='logoz'>
                                    <div className='icon-contact'>
                                        <a href={window.location.origin}>
                                        {common.header.logo!=''&&<img src={common.header.logo} className="header-logo-dark" alt="alt logo here"/>}
                                        </a>
                                        <ul className='ulicon'>
                                            <li className="bnb">
                                                <a href={common.lien_he.url_fb}>
                                                    {get_icon("facebook","#a81411","15px",{marginRight:"1px"})}
                                                </a>
                                            </li>
                                            <li className="bnb">
                                                <a href='#'>
                                                    {get_icon("instagram","#a81411","15px",{marginRight:"1px"})}
                                                </a>
                                            </li>
                                            <li className="bnb">
                                                <a href='#'>
                                                    {get_icon("youtube","#a81411","15px",{marginRight:"1px"})}
                                                </a>
                                            </li>
                                            <li className="bnb">
                                                <a href='#'>
                                                    {get_icon("ping","#a81411","15px",{marginRight:"1px"})}
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                    <p style={{color:'rgb(244 235 191)',fontSize:'14px',textAlign:'center'}}><strong>a ch</strong> : {common.lien_he.dia_chi}</p>

                                </div>
                            </div>
                            <div className='col-12 col-sm-6 col-lg-3'>
                                <div className='aboutz'>
                                    <span className='titlez'>Thng tin</span>
                                    <ul className='listz'>
                                        {
                                            common.footer.about.map((item,i)=>{
                                                return (
                                                    <li key={i}><a href={item.url} title={item.name}>{item.name}</a></li>
                                                )
                                            })
                                        }
                                    </ul>
                                </div>
                            </div>
                            <div className='col-12 col-sm-6 col-lg-3'>
                                <div className='aboutz'>
                                    <span className='titlez'>CHNH SCH</span>
                                    <ul className='listz'>
                                        {
                                            common.footer.privacy.map((item,i)=>{
                                                return (
                                                    <li key={i}><a href={item.url} title={item.name}>{item.name}</a></li>
                                                )
                                            })
                                        }
                                    </ul>
                                </div>
                            </div>
                            <div className='col-12 col-sm-6 col-lg-3'>
                                <div className='aboutz'>
                                    <span className='titlez'>TUYN DNG</span>
                                    <ul className='listz'>
                                        {
                                            common.footer.job.map((item,i)=>{
                                                return (
                                                    <li key={i}><a href={item.url} title={item.name}>{item.name}</a></li>
                                                )
                                            })
                                        }
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='footer-bottom'>
                    {common.footer.bo_cong_thuong.name!=''&&<div className='container lk textz'>
                        <p>{common.footer.bo_cong_thuong.name}</p>
                        <p><a href={common.footer.bo_cong_thuong.url}><img width={200} src={bct}/></a></p>
                    </div>}
                    <div className='sol textz'>
                        <p>{common.footer.design_by}</p>
                    </div>
                </div>
                <div className="fixed-tool">
                    <ul className='ulz'>
                        <li>
                            <a href={`tel:${common.lien_he.sdt_hotline}`}><i className="icon-phone"
                                style={{backgroundImage:`url(${phoneIcon})`}}
                            ></i></a>
                            <span className="aml-text-content aml-tooltiptext">Gi ngay: {zalo_number_text}</span>
                        </li>
                        <li>
                            <a target="_blank" href={`https://zalo.me/${common.lien_he.sdt_zalo}`}><i className="icon-zalo"
                            style={{backgroundImage:`url(${ZaloIcon})`}}
                            ></i></a>
                            <span className="aml-text-content aml-tooltiptext">Chat vi chng ti qua Zalo</span>
                        </li>
                        <li>
                            <a target="_blank" href={common.lien_he.url_fb}><i className="icon-messenger"
                            style={{backgroundImage:`url(${fbIcon})`}}
                            ></i></a>
                            <span className="aml-text-content aml-tooltiptext">Facebook Messenger</span>
                        </li>
                    </ul>
                </div>
                <div className='contacts'>
                    <div className='phone-call'>
                          <a href={`tel:${common.lien_he.sdt_hotline}`} className='icon-contactz' style={{width:"92px"}} target="_blank">
                              <div className='calzs'>
                                   <img src={call} />
                              </div>
                              <span className='phz'>{zalo_number_text}</span>
                         </a>
                    </div>
               </div>
            </footer>
        </React.Fragment>
    )
  }
}