import React, { Component } from 'react';
import './Category_body.css'
import Card_slide from '../material/contents_category/card_slide';
import {get_icon} from '../../lib/fs'
export default class Category_body extends Component {
  constructor (props) {
    super(props)
    this.state = {}
  }
  render() {
    let {data,common}=this.props;
    return (
      <main className='mainz'>
          {data.phu.show_slide==true&&<div>
            <p className='headhome'><span></span> <b style={{padding:"0px 20px"}}>Sn phm ni bt </b> <span></span></p>
          </div>}
          <div className= {data.phu.show_slide==true?'container rvert':'container rvertx'}>
            <section className='contents'>
              <h1 className='title'>{data.contents.title}</h1>
              {data.phu.show_slide==true&&<div className='price'>
                Gi : <span className='insz'><ins>{Number(data.contents.price_ins).format(0, 3, '.', ',')}  </ins></span>
                <span style={{display:'none'}}>Cn hng</span>
              </div>}
              <div className='short-des'>
                {data.contents.short_des}
              </div>
              <div className='long-des'   dangerouslySetInnerHTML={{__html:data.contents.long_des}}>
              </div>
            </section>
            {data.related_posts.length>0&&<section>
              <h2 className='headhome'><span></span> <b style={{margin:"0px 20px"}}>{get_icon("1star","white","30px")} Bi vit lin quan {get_icon("1star","white","30px")}</b> <span></span></h2>
                <ul className='lis-news row'>
                    {
                      data.related_posts.map((item,i)=>{
                        return (
                          <li className='title-news col-12 col-sm-6 col-xl-4' key={i}>
                            <a href={item.link}>{item.title}</a>
                          </li>
                        )
                      })
                    }
                </ul>
            </section>}
            {data.phu.show_slide==true&&<section>
                <Card_slide
                  cart_sp={data.cart_sp}
                  common={common}

                />
            </section>}
          </div>
      </main>
    );
  }
}

